<template>
	<div class="case-box draggable-resizable">
		<div class="controls-list">
			<div :class="commonClassName" @click="addControl(1)" class="controls-item controls-text">
				<p></p>
				<!-- <vue-draggable-resizable
						ref="textControl"
						:h="30"
						:w="120"
						:x="controlTxtPos.x"
						:y="controlTxtPos.y"
						:z="999"
						class-name-dragging="lq-dragging-class"
						:handles="[]"
						@dragstop="onTextDragStop"
				>
				</vue-draggable-resizable> -->
			</div>
			<div :class="commonClassName" class="controls-item controls-date" @click="addControl(2)"></div>
			<div :class="commonClassName" class="controls-item controls-select" @click="addControl(3)"></div>
			<div :class="commonClassName" class="controls-item controls-sign" @click="addControl(4)"></div>
			<div :class="commonClassName" class="controls-item controls-seal" @click="addControl(5)"></div>
		</div>
		<div class="drag-wrap">
			<vue-draggable-resizable
				v-for="(item, index) in controlsArr"
				:key="item.customId"
				:ref="item.customId"
				:custom-id="item.customId"
				:h="item.fontsSize | filterFonstSizeToHeight(item)"
				:w="item.width"
				:x="item.x"
				:y="item.y"
				:minWidth="200"
				:parentH="600"
				:parentW="800"
				:parent="true"
				:common-class-name="commonClassName"
				:class-name="item.type | filterAccordingToType"
				:class-name-active="activedClass"
				:handles="item.type | filterAccordingToType('handles')"
				@resizestop="onResizstop"
				@dragstop="onDragstop"
				@activated="onActivated"
			>
			<div :class="commonClassName" class="inner-container">
					{{ filterSignatory(item.signatory) }}<span class="name">({{ item.name }})</span>
			</div>
			<i class="close el-icon-error" @click="removeControl(item.customId)"></i>
			<div class="seal-inner" :class="commonClassName">
				<div class="seal" :class="commonClassName"></div>
			</div>
			</vue-draggable-resizable>
		</div>
		<div class="set-wrap">
			<div class="control-set-item">
				<p class="title"></p>
				<el-input size="mini" v-model="textVal" @change="textChange"></el-input>
			</div>
				<div class="control-set-item" v-if="['text', 'date', 'select'].includes(activedType)">
					<p class="title"></p>
					<el-select size="mini" v-model="fontSizeValue" @change="selectFontsize" placeholder="">
						<el-option
							v-for="item in fontSizeOptions"
							:key="item.value"
							:label="item.label"
							:value="item.value"
						>
						</el-option>
					</el-select>
				</div>
				<div class="control-set-item">
					<p class="title"></p>
					<el-select
						size="mini"
						v-model="signatory"
						@change="selectSignatory"
						placeholder=""
					>
						<el-option
							v-for="item in signatoryOptions"
							:key="item.value"
							:label="item.label"
							:value="item.value"
						>
						</el-option>
					</el-select>
				</div>
		</div>
	</div>
</template>

<script>
import VueDraggableResizable from '@/components/vue-draggable-resizable';
import { findIndex, find } from 'lodash';
import { wordFontSizeToPx } from '@/utils';
export default {
  components: {
		VueDraggableResizable
	},
	data() {
		return {
			activedId: '', // id
			activedType: '', // 
			commonClassName: 'target', // ,
			activedClass: 'lq-active-class',
			controlsArr: [], // 
			fontSizeOptions: [
				{
					label: '',
					value: 0
				},
				{
					label: '',
					value: 1
				},
				{
					label: '',
					value: 2
				},
				{
					label: '',
					value: 3
				},
				{
					label: '',
					value: 4
				},
				{
					label: '',
					value: 5
				},
				{
					label: '',
					value: 6
				},
				{
					label: '',
					value: 7
				},
				{
					label: '',
					value: 8
				},
				{
					label: '',
					value: 9
				},
				{
					label: '',
					value: 10
				},
				{
					label: '',
					value: 11
				}
			],
			signatoryOptions: [],
			textVal: '',
			fontSizeValue: 10, // 5 14px
			signatory: 0, // 
			minLeft: 0, // 
			controlTxtPos: {
				x: 60,
				y: 0
			}
		}
	},
	filters: {
		filterAccordingToType(controlsType, type) {
			let className = '';
			let handles = [];
			switch (controlsType) {
				case 'text':
					className = 'lq-draggable-text';
					handles = ['mr'];
					break;
				case 'seal':
					className = 'lq-draggable-seal';
					handles = [];
					break;
				case 'sign':
					className = 'lq-draggable-sign';
					handles = [];
					break;
				case 'date':
					className = 'lq-draggable-date';
					handles = [];
					break;
				case 'select':
					className = 'lq-draggable-select';
					handles = [];
					break;
				default:
					break;
			}
			if (type && type === 'handles') {
				return handles;
			} else {
				return className;
			}
		},
		filterFonstSizeToHeight(fontSize, item) {
			if (item.type === 'sign' || item.type === 'seal') {
				return item.height // 
			}
			return wordFontSizeToPx(fontSize) + 10 // 5px padding
		}
	},
	computed: {
		filterSignatory() {
			return function(signatory) {
				let findItem = find(this.signatoryOptions, o => {
					return signatory === o.value
				})
				return findItem.label
			}
		}
	},
	created() {
		this.signatoryOptions = [
			{
				label: '',
				value: 0
			},
			{
				label: '',
				value: 1
			},
			{
				label: '',
				value: 2
			}
		]
	},
	mounted() {
		this.getDomData();
	},
	// 
	methods: {
		// 
		getDomData() {
			let domCtrList = document.getElementsByClassName('controls-list')[0]
			let domFile = document.getElementsByClassName('drag-wrap')[0]
			this.minLeft = domFile.offsetLeft
		},
		onTextDragStop(left, top) {
			if (left >= this.minLeft) {
				// 
				this.addControl(1, left - this.minLeft, top);
			}
			this.$refs.textControl.setPosByHands(left, top);
			console.log(this.controlTxtPos);
		},
		// 
		onActivated(customId) {
			// console.log(customId);
			//  
			this.activedId && this.setVal(this.textVal);
			this.activedId = customId;
			let editIndex = this.getEditIndex();
			this.activedType = this.controlsArr[editIndex].type;
			this.textVal = this.controlsArr[editIndex].name; // 
			this.signatory = this.controlsArr[editIndex].signatory; // 
			// 
			if (['text', 'date', 'select'].includes(this.activedType)) {
					this.fontSizeValue = this.controlsArr[editIndex].fontsSize;
			} else {
					this.fontSizeValue = 10;
			}
		},
		// 
		onResizstop(left, top, width, height, customId) {
			// console.log(left, top, width, height, customId);
		},
		// 
		onDragstop(left, top, customId) {
			// console.log(left, top, customId);
		},
		// 
		textChange(val) {
			this.setVal(val);
		},
		setVal(val) {
			let editIndex = this.getEditIndex()
			this.controlsArr[editIndex].name = val
		},
		// 
		selectFontsize(fontsSize) {
			let editIndex = this.getEditIndex()
			this.controlsArr[editIndex].fontsSize = fontsSize
		},
		// 
		selectSignatory(signatory) {
			let editIndex = this.getEditIndex()
			this.controlsArr[editIndex].signatory = signatory
		},
		// Index
		getEditIndex() {
			return findIndex(this.controlsArr, o => {
				return o.customId === this.activedId;
			})
		},
		// 
		addControl(type, x, y) {
			const controlObjMap = {
				1: {
					customId: Date.now(),
					width: 200,
					height: 30,
					x: x,
					y: y,
					className: 'lq-draggable-text',
					type: 'text', // 
					handles: ['mr'],
					name: '',
					fontsSize: 10,
					signatory: 0 // 
				},
				2: {
					customId: Date.now(),
					width: 80,
					height: 80,
					x: 0,
					y: 140,
					type: 'seal', // 
					name: '',
					signatory: 0 // 
				},
				3: {
					customId: Date.now(),
					width: 80,
					height: 40,
					x: 0,
					y: 260,
					type: 'sign', // 
					name: '',
					signatory: 0 // 
				},
				4: {
					customId: Date.now(),
					width: 80,
					height: 30,
					x: 0,
					y: 380,
					type: 'date', // 
					name: '',
					fontsSize: 10,
					signatory: 0 // 
				},
				5: {
					customId: Date.now(),
					width: 80,
					height: 30,
					x: 200,
					y: 380,
					type: 'select', // 
					name: '',
					fontsSize: 10,
					signatory: 0 // 
				}
			}
			this.controlsArr.push(controlObjMap[type])
			this.setControlActive(controlObjMap[type].customId)
		},
		// 
		setControlActive(customId) {
			this.$nextTick(() => {
				// console.log(this.$refs[customId][0])
				this.$refs[customId][0].elementDown()
			})
		},
		removeControl(customId) {
			let editIndex = findIndex(this.controlsArr, o => {
					return o.customId === customId
			})
			this.controlsArr.splice(editIndex, 1)
			this.activedId = ''
			this.activedType = ''
			this.textVal = '' // 
			this.signatory = 0 // 
			this.fontSizeValue = 10
		}
	}
}
</script>
<style lang='less' scoped>
//@import url(); css
.draggable-resizable {
	display: flex;
	justify-content: space-between;
	.controls-list {
		width: 220px;
		padding: 0 10px;
		.controls-item {
			height: 30px;
			border: solid 1px #ccc;
			margin-bottom: 20px;
			cursor: pointer;
			line-height: 30px;
			text-align: center;
		}
	}
}
.drag-wrap {
	position: relative;
	width: 594px;
	height: 600px;
	margin: 0 20px;
	border: solid 1px #ccc;
	.draggable {
		cursor: move;
		// cursor: pointer;
	}
	.inner-container {
		margin-top: -20px;
		height: 20px;
		font-size: 14px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		position: relative;
		.name {
			font-size: 12px;
		}
	}
	.el-icon-error {
		font-size: 16px;
		position: absolute;
		top: -20px;
		right: -10px;
		z-index: 100;
		cursor: pointer;
	}

	.lq-draggable-text,
	.lq-draggable-date,
	.lq-draggable-sign,
	.lq-draggable-select { border: dashed 1px #000; }
  .lq-draggable-seal {
		.seal-inner {
			width: 100%;
			height: 100%;
			border: dashed 1px #000;
			.seal {
				width: calc(100% - 12px);
				height: calc(100% - 12px);
				margin: 5px 0 0 5px;
				border: dashed 1px #000;
				border-radius: 50%;
			}
		}
	}
	.lq-active-class {
		border-color: rgb(14, 74, 238);
		.seal-inner {
			border-color: rgb(14, 74, 238);
			.seal {
				border-color: rgb(14, 74, 238);
			}
		}
	}
}
.set-wrap {
	width: 230px;
	padding: 0 10px;
	.control-set-item {
		margin-bottom: 20px;
		.title {
			margin-bottom: 5px;
			font-size: 14px;
		}
	}
}
.lq-dragging-class {
  border: dashed 1px #000;
}
</style>
<style lang="less">
.lq-active-class {
	.handle.handle-mr {
		border-radius: 50%;
		width: 20px;
		height: 20px;
		margin-top: -20px;
	}
	.handle-mr::before {
		content: ' ';
		width: 0;
		height: 0;
		border-top: 7px transparent solid;
		border-bottom: 7px transparent solid;
		border-right: 6px solid rgb(122, 121, 121);
		position: absolute;
		left: 2px;
		top: 2px;
	}
	.handle-mr::after {
		content: ' ';
		width: 0;
		height: 0;
		border-top: 7px transparent solid;
		border-bottom: 7px transparent solid;
		border-left: 6px solid rgb(122, 121, 121);
		position: absolute;
		right: 2px;
		top: 2px;
	}
}
</style>
